import React from 'react';
import ImageCarousel from './Components/ImageCarousel';
import BlogsArticlesTab from './Components/BlogsArticlesTab';
import Grid from '@mui/material/Grid';
import Services from './Components/Services';
import PopUpBeggining from '../Common/PopUpBeggining';
import SectionGroupSM from './Components/SectionGroupSM';

export default function Home(props) {
    console.log("Home");
    return (
        <div>
            <PopUpBeggining />
            <ImageCarousel />
            <Grid container>
                <Grid item xs={12} sm={12} md={8}>
                    <SectionGroupSM />
                    <BlogsArticlesTab />
                </Grid>
                <Grid item xs={12} sm={12} md={4}>
                    <Services />
                </Grid>
            </Grid>
        </div>
    )
}