import { useState } from "react";
import { Button, Paper, Typography } from "@mui/material";
import Rating from '@mui/material/Rating';
import SendIcon from '@mui/icons-material/Send';
import theme from '../../theme'

export default function FeedbackForm(props) {
    const containerStyle = {
        fontSize: '14px',
        padding: '10px 4px',
        margin: '4px',
    }
    const feedbacksStyle = {
        backgroundColor: theme.white,
        borderRadius: '8px',
        border: '1px solid ' + theme.border,
    }
    const inputContainerStyle = {
        padding: '8px 8px',
    }
    const inputFieldStyle = {
        border: '1px solid ' + theme.border,
        backgroundColor: theme.light,
        borderRadius: '8px',
        padding: '8px',
        width: '100%',
        boxSizing: "border-box",
    }
    const sendButtonStyle = {
        backgroundColor: '#3d3644',
        color:theme.white,
        textTransform:'none',
    }

    const [ratingValue, setRatingValue] = useState(0);

    return (
        <div style={containerStyle}>
            <Paper style={feedbacksStyle} >
                <Typography style={{fontWeight:700, padding: '8px 8px 0 8px'}}>Give Feedback: </Typography>
                <div style={inputContainerStyle}>
                    <label for="name">Name</label>
                    <input
                        style={inputFieldStyle}
                        placeholder="Name"
                        id="name"
                    />
                </div>
                <div style={{padding: '8px 8px 0px'}}>
                    <label for="selectRating">Select Rating:</label>
                    <br />
                    <Rating
                        id="selectRating"
                        value={ratingValue}
                        onChange={(event, newValue) => {
                            setRatingValue(newValue);
                        }}
                    />
                </div>
                <div style={inputContainerStyle}>
                    <label for="details">Details</label>
                    <textarea 
                        id="details" 
                        rows="4"
                        style={inputFieldStyle}
                        placeholder="Details"
                    ></textarea>
                </div>
                <div style={{padding: '0px 8px 8px'}}>
                    <Button 
                        variant="contained" 
                        endIcon={<SendIcon />}
                        style={sendButtonStyle}
                    > Send </Button>
                </div>

            </Paper>
        </div>
    );
}