import { useState, useEffect } from 'react';
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import sectionsHelper from '../Helpers/sectionsHelper';
import { useMediaQuery } from '@mui/material';
import theme from '../theme';

export default function SectionGroupMD(props) {

    const SectionGroupContainer = {
        borderBottom: '1px solid ' + theme.border,
    };

    const buttonStyleMD = (selected) => {
        return (
            {
                color: theme.black,
                borderRadius: '0px',
                borderRight: '1px solid ' + theme.border,
                fontFamily: "Trebuchet MS, Lucida Grande, Lucida Sans Unicode, Lucida Sans, Tahoma, sans-serif",
                textTransform: 'none',
                backgroundImage: selected ? '' : `linear-gradient(to bottom, ${theme.dark}, ${theme.light})`,
            }
        )
    }

    const ButtonGroupStyleMD = {

    }

    const buttonStyleSM = (selected) => {
        return (
            {
                color: theme.black,
                borderRadius: '0px',
                borderBottom: '1px solid ' + theme.border,
                fontFamily: "Trebuchet MS, Lucida Grande, Lucida Sans Unicode, Lucida Sans, Tahoma, sans-serif",
                textTransform: 'none',
                backgroundImage: selected ? '' : `linear-gradient(to bottom, ${theme.dark}, ${theme.light})`,
            }
        )
    }

    const buttomGroupStyleSM = {
        width: '100%',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'stretch',
    }

    // Loading different component for less than 600px size screen
    const isMd = useMediaQuery('(min-width:600px)');
    const [currentColorIndex, setCurrentColorIndex] = useState(0);

    const colors = ['#ff0000', '#0000ff', '#ff3c00', '#9e0299'];
    useEffect(() => {
        const intervalId = setInterval(() => {
            setCurrentColorIndex((prevIndex) => (prevIndex + 1) % colors.length);
        }, 200);
        return () => clearInterval(intervalId);
    }, [currentColorIndex, colors.length]);
    
    const labelStyle = {
        color: colors[currentColorIndex],
        display: 'inline-block',
        marginLeft: '8px', // Add some spacing between the label and the section
    };

    return (
        <div style={SectionGroupContainer}>
            {
                isMd ? (
                    <ButtonGroup variant="text" aria-label="text button group" style={ButtonGroupStyleMD}>
                        {
                            Object.keys(sectionsHelper).map((key) => {
                                const selected = window.location.href.includes(sectionsHelper[key].url);
                                return (
                                    <Button
                                        onClick={() => window.open(sectionsHelper[key].url, "_self")}
                                        style={buttonStyleMD(selected)}
                                    >{key}
                                    </Button>
                                );
                            })
                        }
                    </ButtonGroup>
                ) : (
                    // <ButtonGroup orientation="vertical" variant="text" aria-label="text button group" style={buttomGroupStyleSM}>
                    //     {
                    //         Object.keys(sectionsHelper).map((key) => {
                    //             const selected = window.location.href.includes(sectionsHelper[key].url);
                    //             const bookOrEmer = (key == "Book Appoinment" || key == "Emergency Contact" ? true : false);
                    //             return (
                    //                 <Button
                    //                     onClick={() => window.open(sectionsHelper[key].url, "_self")}
                    //                     style={buttonStyleSM(selected)}
                    //                 >{key}
                    //                 {
                    //                         bookOrEmer ? (
                    //                             <span style={labelStyle}>Favourite</span>
                    //                         ) : (<></>)
                    //                     }</Button>
                    //             );
                    //         })
                    //     }
                    // </ButtonGroup>
                    <></>
                )
            }
        </div>
    );
}