import React from 'react';
import { AppBar, Toolbar, Typography, Box } from '@mui/material';
import LegalAdviceLogo from '../Images/LegalAdvice.png';
import theme from '../theme';

export default function Header(props) {

    const HeaderStyle = {
        backgroundColor: theme.light,
        boxShadow:'none',
        borderBottom: '1px solid ' + theme.border,  // Border style and color
    };

    return (
        <AppBar position="static" style={HeaderStyle}>
            <Toolbar style={{padding:'0px 16px'}}>
                <Box>
                    <img src={LegalAdviceLogo} alt="Left Image" style={{ height: 50, marginLeft: 0, marginTop: 5 }} />
                </Box>
            </Toolbar>
        </AppBar>
    );
};
