import { Divider, Paper, Typography } from "@mui/material";
import legalActsHelper from "../../Helpers/legalActsHelper";
import theme from "../../theme";

function LegalActElement(props) {
    const legalActElementStyle = {
        fontSize: '14px',
        padding: "12px 8px",
    }
    const detailsStyle = {
        textAlign: "justify",
        cursor:'pointer'
    }
    return (
        <div style={legalActElementStyle}>
            <div style={detailsStyle}>{props.legalAct.details}</div>
        </div>
    )
}

export default function LegalActsList(props) {
    const containerStyle = {
        fontSize: '14px',
        padding: '10px 4px',
        margin: '4px',
    }
    const legalActListStyle = {
        backgroundColor: theme.white,
        borderRadius: '8px',
        border: '1px solid ' + theme.border,
    }
    return (
        <div style={containerStyle}>
            <Paper style={legalActListStyle}>
            <Typography style={{ fontWeight: 700, padding: '8px 8px 0 8px' }}>Legal Acts: </Typography>
                {
                    legalActsHelper.map((legalAct) => {
                        return (
                            <>
                                <a href={legalAct.url}>
                                    <LegalActElement legalAct={legalAct}/>
                                </a>
                                <Divider />
                            </>
                        )
                    })
                }
            </Paper>
        </div>
    )
}