import theme from '../theme'

export default function Topper(props) {

    const topperContainerStyle = {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        padding: '2px', // Adjust the padding as needed
        fontSize: '14px',
        borderBottom: '1px solid ' + theme.border,  // Border style and color
        backgroundImage: `linear-gradient(to bottom, ${theme.light}, ${theme.dark})`, // light gray to Dark gray gradient
        color: theme.black, // Text color, adjust as needed
    };

    const countStyle = {
        // textAlign: 'center',
        margin: '0 10px', // Adjust the margin as needed
        fontWeight: '700',
    };

    return (
        <div style={topperContainerStyle}>
            <div style={countStyle}>
                Explored by <span style={{fontSize: '20px', color:'darkblue'}}>1000+</span>
                {/* Explore by <span style={{fontSize: '20px', color:'darkblue'}}>1000+</span> visitors */}
                {/* Total Views:  Replace with actual total views count */}
            </div>
            
            <div style={countStyle}>
                {/* Contacted People: <span style={{fontSize: '18px'}}>500</span> Replace with actual contacted people count */}
                Assisted <span style={{fontSize: '20px', color:'darkblue'}}>500+</span>
                {/* Helping <span style={{fontSize: '20px', color:'darkblue'}}>500+</span> individuals and counting */}
            </div>
        </div>
    );
}