import { Divider, Paper, Typography } from "@mui/material";
import judgementsHelper from "../../Helpers/judgementsHelper";
import theme from "../../theme";

function JudgementElement(props) {
    const judgementElementStyle = {
        fontSize: '14px',
        padding: "12px 8px",
    }
    const detailsStyle = {
        textAlign: "justify",
        cursor:'pointer'
    }
    return (
        <div style={judgementElementStyle}>
            <div style={detailsStyle}>{props.judgement.details}</div>
        </div>
    )
}

export default function JudgementsList(props) {
    const containerStyle = {
        fontSize: '14px',
        padding: '10px 4px',
        margin: '4px',
    }
    const judgementListStyle = {
        backgroundColor: theme.white,
        borderRadius: '8px',
        border: '1px solid ' + theme.border,
    }
    return (
        <div style={containerStyle}>
            <Paper style={judgementListStyle}>
            <Typography style={{ fontWeight: 700, padding: '8px 8px 0 8px' }}>Judgements Of Supreme Court: </Typography>
                {
                    judgementsHelper.map((judgement) => {
                        return (
                            <>
                                <a href={judgement.url}>
                                    <JudgementElement judgement={judgement}/>
                                </a>
                                <Divider />
                            </>
                        )
                    })
                }
            </Paper>
        </div>
    )
}