import { Divider, Paper, Typography } from "@mui/material";
import notificationHelper from "../../Helpers/notificationsHelper";
import theme from "../../theme";

function NotificationElement(props) {
    const notificationElementStyle = {
        fontSize: '14px',
        padding: "12px 8px",
    }
    const detailsStyle = {
        textAlign: "justify",
        cursor:'pointer'
    }
    return (
        <div style={notificationElementStyle}>
            <div style={detailsStyle}>{props.notification.details}</div>
        </div>
    )
}

export default function NotificationsList(props) {
    const containerStyle = {
        fontSize: '14px',
        padding: '10px 4px',
        margin: '4px',
    }
    const notificationListStyle = {
        backgroundColor: theme.white,
        borderRadius: '8px',
        border: '1px solid ' + theme.border,
    }
    return (
        <div style={containerStyle}>
            <Paper style={notificationListStyle}>
            <Typography style={{ fontWeight: 700, padding: '8px 8px 0 8px' }}>Notifications: </Typography>
                {
                    notificationHelper.map((notification) => {
                        return (
                            <>
                                <a href={notification.url}>
                                    <NotificationElement notification={notification}/>
                                </a>
                                <Divider />
                            </>
                        )
                    })
                }
            </Paper>
        </div>
    )
}