
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import servicesHelper from '../../Helpers/servicesHelper';
import { Box } from '@mui/material';
import theme from '../../theme'

export default function Services(props) {

    const buttonStyle = {
        color: theme.black,
        borderRadius: '0px',
        border: '1px solid ' + theme.border,
        borderTop: '0px',
        fontFamily: "Trebuchet MS, Lucida Grande, Lucida Sans Unicode, Lucida Sans, Tahoma, sans-serif",
        textTransform: 'none',
        backgroundImage: `linear-gradient(to bottom, ${theme.dark}, ${theme.light})`,
        justifyContent: "flex-start"
    }

    const buttomGroupStyle = {
        width: '100%',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'stretch',
    }

    const containerStyle = {
        fontSize: '14px',
        padding: '10px 4px',
        margin: '4px',
    }

    const topBoxStyle = {
        color: theme.white,
        borderRadius: '8px 8px 0 0',
        borderBottom: '1px solid ' + theme.border,
        fontFamily: "Trebuchet MS, Lucida Grande, Lucida Sans Unicode, Lucida Sans, Tahoma, sans-serif",
        textTransform: 'none',
        backgroundColor: '#3d3644',
        textAlign: 'center',
        padding:'8px 16px',
    }

    const buttonStyleLast = {
        color: theme.black,
        borderRadius: '0 0 8px 8px',
        border: '1px solid ' + theme.border,
        borderTop: '0px',
        fontFamily: "Trebuchet MS, Lucida Grande, Lucida Sans Unicode, Lucida Sans, Tahoma, sans-serif",
        textTransform: 'none',
        backgroundImage: `linear-gradient(to bottom, ${theme.dark}, ${theme.light})`,
        justifyContent: "flex-start"
    }

    return (
        <div style={containerStyle}>
            <ButtonGroup orientation="vertical" variant="text" aria-label="text button group" style={buttomGroupStyle}>
                <Box style={topBoxStyle}>
                    Legal Advices
                </Box>
                {
                    servicesHelper.map((service, index, array) => {
                        const isLast = index === array.length - 2;
                        if (service != "Other") {
                            return (
                                <Button onClick={() => window.open("/Book-Appoinment", "_self")} style={isLast? buttonStyleLast : buttonStyle}>{service}</Button>
                            );
                        }
                    })
                }
            </ButtonGroup>
        </div>
    );
}