import { useState } from "react";
import { Button, Paper, Typography } from "@mui/material";
import servicesHelper from "../../Helpers/servicesHelper";
import PhoneForwardedIcon from '@mui/icons-material/PhoneForwarded';
import theme from '../../theme';

export default function EmergencyContactForm(props) {
    const containerStyle = {
        fontSize: '14px',
        padding: '10px 4px',
        margin: '4px',
    }
    const feedbacksStyle = {
        backgroundColor: theme.white,
        borderRadius: '8px',
        border: '1px solid ' + theme.border,
    }
    const inputContainerStyle = {
        padding: '8px 8px',
    }
    const inputFieldStyle = {
        border: '1px solid ' + theme.border,
        backgroundColor: theme.light,
        borderRadius: '8px',
        padding: '8px',
        width: '100%',
        boxSizing: "border-box",
    }
    const sendButtonStyle = {
        backgroundColor: '#3d3644',
        color: theme.white,
        textTransform: 'none',
    }

    const [selectedOption, setSelectedOption] = useState('');
    const handleOptionChange = (event) => {
        setSelectedOption(event.target.value);
    };

    return (
        <div style={containerStyle}>
            <Paper style={feedbacksStyle} >
                <Typography style={{ fontWeight: 700, padding: '8px 8px 0 8px' }}>Contact Urgently: </Typography>
                <div style={inputContainerStyle}>
                    <label for="name">Name *</label>
                    <input
                        style={inputFieldStyle}
                        placeholder="Name"
                        id="name"
                    />
                </div>
                <div style={inputContainerStyle}>
                    <label for="mobile">Mobile No *</label>
                    <input
                        style={inputFieldStyle}
                        placeholder="Mobile No"
                        id="mobile"
                    />
                </div>
                <div style={inputContainerStyle}>
                    <div style={{paddingBottom: '4px'}}>Type of issue: *</div>
                    {
                        servicesHelper.map((service) => {
                            return (
                                <>
                                    <label>
                                        <input
                                            type="radio"
                                            value={service}
                                            checked={selectedOption === service}
                                            onChange={handleOptionChange}
                                            onClick={() => { 
                                                if(service == "Other"){ 
                                                    document.getElementById("details").focus(); 
                                                }
                                            }}
                                        />
                                        {service}
                                    </label>
                                    <br />
                                </>
                            )
                        })
                    }
                </div>
                <div style={inputContainerStyle}>
                    <label for="details" id="detailsRequire">Details</label>
                    <textarea
                        id="details"
                        rows="4"
                        style={inputFieldStyle}
                        placeholder="Details"
                    ></textarea>
                </div>
                <div style={{ padding: '0px 8px 8px' }}>
                    <Button
                        variant="contained"
                        endIcon={<PhoneForwardedIcon />}
                        style={sendButtonStyle}
                    > Contact Now </Button>
                </div>

            </Paper>
        </div>
    );
}