import { useState } from 'react'
import Carousel from 'react-material-ui-carousel'
import { Paper, Button } from '@mui/material'
import SupremeCourt from '../../Images/1.jpg'
import LegalDesk from '../../Images/2.jpg'
import HighCourt from '../../Images/3.jpg'
import Consult from '../../Images/4.png'
import Advertisement from '../../Images/5.png'

export default function ImageCarousel(props) {

    const [firstImgLoaded, setFirstImgLoaded] = useState(false);

    var items = [
        {
            name: "Consulting a Client",
            imageUrl: Consult,
        },
        {
            name: "Indian Supreme Court",
            imageUrl: SupremeCourt,
        },
        {
            name: "Legal advice desk",
            imageUrl: LegalDesk,
        },
        {
            name: "Gujarat High court",
            imageUrl: HighCourt,
        },
        {
            name: "Booking Advertisement",
            imageUrl: Advertisement,
        },
    ]

    return (
        <>
            <img
                src={items[0].imageUrl}
                onLoad={() => setFirstImgLoaded(true)}
                style={{ display: "none" }}
            />

            {firstImgLoaded && (
                <Carousel
                    next={(next, active) => { }}
                    prev={(prev, active) => { }}
                    animation="slide"
                >
                    {
                        items.map((item, i) => <Item key={i} item={item} />)
                    }
                </Carousel>
            )}
        </>
    )
}

function Item(props) {
    const itemStyle = {
        margin: '8px',
        borderRadius: '8px',
    }
    return (
        <Paper style={itemStyle}>
            <img
                src={props.item.imageUrl}
                alt={props.item.name}
                style={{ width: '100%', borderRadius: '8px' }}
            />
        </Paper>
    )
}