import { Divider, Paper, Typography } from "@mui/material";
import feedbacksHelper from "../../Helpers/feedbacksHelper";
import Rating from '@mui/material/Rating';
import theme from "../../theme";

function Feedback(props) {
    const feedbackStyle = {
        fontSize: '14px',
        padding: "12px 8px",
    }
    const nameStyle = {
        fontWeight:700
    }
    const detailsStyle = {
        textAlign: "justify",
    }
    return (
        <div style={feedbackStyle}>
            <div style={nameStyle}>{props.feedback.name}</div>
            <Rating name="read-only" value={props.feedback.ratings} readOnly />
            <div style={detailsStyle}>{props.feedback.details}</div>
        </div>
    )
}

export default function Feedbacks(props) {
    const containerStyle = {
        fontSize: '14px',
        padding: '10px 4px',
        margin: '4px',
    }
    const feedbacksStyle = {
        backgroundColor: theme.white,
        borderRadius: '8px',
        border: '1px solid ' + theme.border,
    }
    return (
        <div style={containerStyle}>
            <Paper style={feedbacksStyle}>
                <Typography style={{ fontWeight: 700, padding: '8px 8px 0 8px' }}>Feedbacks: </Typography>
                {
                    feedbacksHelper.map((feedback) => {
                        return (
                            <>
                                <Feedback feedback={feedback} />
                                <Divider />
                            </>
                        )
                    })
                }
            </Paper>
        </div>
    )
}