import React, { useState } from 'react';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import imageUrl from '../Images/pop-up-ad1.png';
import theme from '../theme';

export default function PopUpBeggining(props) {
    const [open, setOpen] = useState(true);
    const handleClose = () => setOpen(false);
    return (
        <Dialog open={open} onClose={handleClose} maxWidth="false">
            <IconButton
                edge="end"
                color="inherit"
                onClick={handleClose}
                aria-label="close"
                sx={{ position: 'absolute', top: 0, right: 12 }}
            >
                <CloseIcon
                    style={{color: theme.white}}
                />
            </IconButton>
            <img 
                src={imageUrl} 
                alt="Get the personalized legal and expert advice from the advocates of Hon'ble High Court and Supreme Court on your doorstep at 299 only" 
                style={{ maxWidth: '500px', cursor:'pointer' }} 
                onClick={() => window.open("/Book-Appoinment", "_self")}
            />
        </Dialog>
    );
};
