import './App.css';
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Topper from './Common/Topper';
import Header from './Common/Header';
import Footer from './Common/Footer';
import SectionGroupMD from './Common/SectionGroupMD';
import sectionsHelper from './Helpers/sectionsHelper';
import Home from './Sections/Home';
import theme from './theme';

function App() {
    const appContainerStyle = {
        maxWidth: 950,
        margin: '0 auto', // Center the container horizontally
        width: '100%',     // Ensure the container takes full width if the screen is smaller than 800px
        border: '1px solid ' + theme.border,  // Border style and color
        backgroundColor: theme.light,
    };

    return (
        <div style={appContainerStyle}>
            <BrowserRouter>
                <Topper />
                <Header />
                <SectionGroupMD />
                <Routes>
                    <Route path="/" element={<Home />} />
                    {
                        Object.keys(sectionsHelper).map((key) => {
                            return (
                                <Route path={sectionsHelper[key].url} element={sectionsHelper[key].element} />
                            );
                        })
                    }
                </Routes>
                <Footer />
            </BrowserRouter>
        </div>
    );
}

export default App;
