import React, { useState } from 'react';
import { Tabs, Tab, Box, Typography, Button, Divider } from '@mui/material';
import { Link } from 'react-router-dom';
import theme from '../../theme';

export default function BlogsArticlesTab(props) {

    const BlogComponent = (props) => {

        const blogBoxStyle = {
            fontSize: '14px',
            padding: '10px 4px',
        }

        return (
            <>
                <Box style={blogBoxStyle}>
                    {props.blog.type == 'blog' ? <Box
                        component="div"
                        sx={{
                            display: 'inline',
                            p: "6px",
                            m: 1,
                            bgcolor: theme.object,
                            borderRadius: 2,
                            fontSize: '12px',
                            fontWeight: '700',
                            color: theme.white,
                            textDecoration: 'none'
                        }}
                    >
                        Blog
                    </Box>
                        :
                        <Box
                            component="div"
                            sx={{
                                display: 'inline',
                                p: "6px",
                                m: 1,
                                bgcolor: '#3d3644',
                                borderRadius: 2,
                                fontSize: '12px',
                                fontWeight: '700',
                                color: theme.white,
                                textDecoration: 'none'
                            }}
                        >
                            Article
                        </Box>}
                    <a href={props.blog.url}>
                        {props.blog.title} {props.blog.date}
                    </a>
                </Box>
                <Divider color={theme.black} />
            </>
        );
    };

    const tabStyle = {
        borderRadius: '10px 10px 0 0',
        backgroundColor: theme.white,
        color: theme.black,
        border: '1px solid ' + theme.border,
        borderBottom: '0px',
        textTransform: 'none',
        fontFamily: "Trebuchet MS, Lucida Grande, Lucida Sans Unicode, Lucida Sans, Tahoma, sans-serif",
    };

    const containerStyle = {
        margin: '8px',
    }

    const viewStyle = {
        borderRadius: '0 8px 8px 8px',
        backgroundColor: theme.white,
        color: theme.black,
        border: '1px solid ' + theme.border,
        padding: '8px',
        height: "200px",
        overflow: "hidden"
    }

    const blogs = [
        {
            title: "Misc. Notification Notice regarding non availability of Hon'ble The Chief Justice on 05/01/2024.",
            url: "",
            date: "[05/01/2024]",
            type: 'Article',
        },
        {
            title: "Misc. Notification Notice regarding non availability of Hon'ble Mr.Justice Aniruddha P. Mayee on 05-01-2024.",
            url: "",
            date: "[04/01/2024]",
            type: 'blog',
        },
        {
            title: "Misc. Notification Notice regarding non-availability of Hon'ble Mr.Justice Ilesh J.Vora on 05-01-2024.",
            url: "",
            date: "[03/01/2024]",
            type: 'blog',
        },
        {
            title: "Misc. Notification Notice regarding availability of Hon'ble on 05-01-2024.",
            url: "",
            date: "[03/01/2024]",
            type: 'blog',
        },
        {
            title: "Misc. Notification Notice regarding Bla bla bla dfnkdnvdfnijdfv n 05-01-2024.",
            url: "",
            date: "[02/01/2024]",
            type: 'blog',
        },
        {
            title: "Misc. Notification Notice regarding non-availability Misc. Notification Notice regarding non-availability 2024.",
            url: "",
            date: "[02/01/2024]",
            type: 'blog',
        },
        {
            title: "Misc. Notification Notice regarding non-availability of Hon'ble Mr.Justice Ilesh J.Vora on 05-01-2024.",
            url: "",
            date: "[02/01/2024]",
            type: 'blog',
        }
    ];

    return (
        <div style={containerStyle}>
            <Button variant="outlined" style={tabStyle}>
                Blogs and Articles
            </Button>
            <Box style={viewStyle} border={1} padding={2}>
                <div className="scrolling-lines">
                    {
                        blogs.map((blog) => {
                            return (
                                <BlogComponent blog={blog} />
                            );
                        })
                    }
                </div>
            </Box>
        </div>
    );
}