import { useState, useEffect } from 'react';
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import sectionsHelper from '../../Helpers/sectionsHelper';
import { Box, useMediaQuery } from '@mui/material';
import theme from '../../theme'

export default function SectionGroupSM(props) {

    const buttonStyle = {
        color: theme.black,
        borderRadius: '0px',
        border: '1px solid ' + theme.border,
        borderTop: '0px',
        fontFamily: "Trebuchet MS, Lucida Grande, Lucida Sans Unicode, Lucida Sans, Tahoma, sans-serif",
        textTransform: 'none',
        backgroundImage: `linear-gradient(to bottom, ${theme.dark}, ${theme.light})`,
        justifyContent: "flex-start"
    }

    const buttonStyleSM = (selected) => {
        return (
            {
                color: theme.black,
                borderRadius: '0px',
                border: '1px solid ' + theme.border,
                borderTop: '0px',
                fontFamily: "Trebuchet MS, Lucida Grande, Lucida Sans Unicode, Lucida Sans, Tahoma, sans-serif",
                textTransform: 'none',
                backgroundImage: `linear-gradient(to bottom, ${theme.dark}, ${theme.light})`,
                justifyContent: "flex-start",
                backgroundImage: selected ? '' : `linear-gradient(to bottom, ${theme.dark}, ${theme.light})`,
            }
        )
    }

    const buttomGroupStyle = {
        width: '100%',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'stretch',
    }

    const containerStyle = {
        fontSize: '14px',
        padding: '10px 4px',
        margin: '4px',
    }

    const topBoxStyle = {
        color: theme.white,
        borderRadius: '8px 8px 0 0',
        borderBottom: '1px solid ' + theme.border,
        fontFamily: "Trebuchet MS, Lucida Grande, Lucida Sans Unicode, Lucida Sans, Tahoma, sans-serif",
        textTransform: 'none',
        backgroundColor: '#3d3644',
        textAlign: 'center',
        padding: '8px 16px',
    }

    const buttonStyleLast = {
        color: theme.black,
        borderRadius: '0 0 8px 8px',
        border: '1px solid ' + theme.border,
        borderTop: '0px',
        fontFamily: "Trebuchet MS, Lucida Grande, Lucida Sans Unicode, Lucida Sans, Tahoma, sans-serif",
        textTransform: 'none',
        backgroundImage: `linear-gradient(to bottom, ${theme.dark}, ${theme.light})`,
        justifyContent: "flex-start"
    }

    const buttonStyleLastSM = (selected) => {
        return (
            {
                color: theme.black,
                borderRadius: '0 0 8px 8px',
                border: '1px solid ' + theme.border,
                borderTop: '0px',
                fontFamily: "Trebuchet MS, Lucida Grande, Lucida Sans Unicode, Lucida Sans, Tahoma, sans-serif",
                textTransform: 'none',
                justifyContent: "flex-start",
                backgroundImage: selected ? '' : `linear-gradient(to bottom, ${theme.dark}, ${theme.light})`,
            }
        )
    }


    const isMd = useMediaQuery('(min-width:600px)');
    const [currentColorIndex, setCurrentColorIndex] = useState(0);

    const colors = ['#ff0000', '#0000ff', '#ff3c00', '#9e0299'];
    useEffect(() => {
        const intervalId = setInterval(() => {
            setCurrentColorIndex((prevIndex) => (prevIndex + 1) % colors.length);
        }, 200);
        return () => clearInterval(intervalId);
    }, [currentColorIndex, colors.length]);

    const labelStyle = {
        color: colors[currentColorIndex],
        display: 'inline-block',
        marginLeft: '8px', // Add some spacing between the label and the section
    };

    if (!isMd) {
        return (
            <div style={containerStyle}>
                <ButtonGroup orientation="vertical" variant="text" aria-label="text button group" style={buttomGroupStyle}>
                    <Box style={topBoxStyle}>
                        Legal Resources
                    </Box>
                    {
                        Object.keys(sectionsHelper).map((key, index, array) => {
                            const isLast = index === array.length - 1;
                            const bookOrEmer = (key == "Book Appoinment" || key == "Emergency Contact" ? true : false);
                            const selected = window.location.href.includes(sectionsHelper[key].url);
                            return (
                                <Button
                                    onClick={() => window.open(sectionsHelper[key].url, "_self")}
                                    style={isLast ? buttonStyleLastSM(selected) : buttonStyleSM(selected)}
                                >{key}
                                    {
                                        bookOrEmer ? (
                                            <span style={labelStyle}>Favourite</span>
                                        ) : (<></>)
                                    }
                                </Button>
                            );
                        })
                    }
                </ButtonGroup>
            </div>
        );
    }
    else {
        return (<></>);
    }
}